<template>
  <div class="content">
    <div class="loading-layer">
      <div class="load_box">
        <div class="top-img-wrapper">
          <img src="../assets/img/user_pic.jpg" alt />
        </div>
        <ul class="use_pass_list">
          <li>
            <input type="text" placeholder="请输入用户名" />
          </li>
          <li>
            <input type="password" placeholder="请输入密码" />
          </li>
        </ul>
        <div class="button_box">
          
          <button class="el-but">登录</button>
          <span>还没有账号？</span>
          <button class="el-but" @click="gotoReg">注册</button>
        </div>
        <ul class="load-bottom-wrapper clearfix">
          <li>
            <a href>意见反馈</a>
          </li>
          <li>|</li>
          <li>
            <a href="##">忘记密码</a>
          </li>
        </ul>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "loading",
  data() {
    return {
      img_url: ""
    };
  },
  methods: {
    gotoReg(){
      this.$router.push("/register")
    }
  }
};
</script>

<style>
@import "../assets/css/index.css";
</style>



