import Vue from 'vue'
import Router from 'vue-router'
import loading from '@/views/loading'
import welcome from '@/views/welcome'
import notFind from '@/views/notFind'
import header from '@/components/header'
import register from '@/views/register'


Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      name: 'index',
      component: loading
    }, {
      path: '/header',
      name: 'header',
      component: header
    }, {
      path: '/loading',
      name: 'loading',
      component: loading
    },
    {
      path: '/register',
      name: 'register',
      component: register
    }, {
      path: '/welcome',
      name: 'welcome',
      component: welcome
    }, {
      path: '*',
      component: notFind

    }
  ]
})



