<template>
  <div class="content overfolw">
    <div class="register-layer">
      <span>欢迎注册</span>
      <span>Fireworks账号</span>
      <div class="regist-from">
        <el-form
          action="##"
          method="post"
          ref="ruleForm"
          label-position="right"
          :rules="rules"
          label-width="120px"
          :model="ruleFormData"
        >
          <el-form-item label="请输入昵称" class="input-400" prop="name">
            <el-input placeholder="请输入昵称" name="name " v-model="ruleFormData.username"></el-input>
          </el-form-item>
          <el-form-item label="请输入邮箱" class="input-400" prop="eMail">
            <el-input placeholder="请输入邮箱" name="eMail " v-model="ruleFormData.eMail"></el-input>
          </el-form-item>
          <el-form-item label="请选择性别" class="input-400" prop="eMail">
          </el-form-item>
          <el-form-item label="请输入密码" class="input-400" prop="oldPassword">
            <el-input
              placeholder="请输入密码"
              type="password"
              name="oldPassword "
              v-model="ruleFormData.oldPassword"
            ></el-input>
          </el-form-item>
          <el-form-item label="再次确定密码" class="input-400" prop="newPassword">
            <el-input
              type="password"
              placeholder="请再次确认密码"
              v-model="ruleFormData.newPassword"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="请选择生日" prop="birthday">
            <el-date-picker
              v-model="ruleFormData.birthday"
              align="right"
              type="date"
              placeholder="选择生日"
              name="birthday"
              :picker-options="pickerOptions"
            ></el-date-picker>
          </el-form-item>
        </el-form>
        <div class="reg-reset-box">
          <button class="reg-reset" @click="resetForm('ruleForm')">重置信息</button>
          <button class="reg-reset" @click="submitForm('ruleForm')">注册</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "loading",
  data() {
    var reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/;
    // 定义了一个正则
    var validateComfirmPwd = (rule, value, callback) => {
      if (!reg.test(value)) {
        callback(new Error("密码应是8-20位数字、字母或字符！"));
      } else if (this.ruleFormData.oldPassword !== value) {
        callback(new Error("两次密码输入不一致！"));
      } else {
        callback();
      }
    };
    return {
      ruleFormData: {
        username: "",
        oldPassword: "",
        newPassword: "",
        birthday: "",
        eMail: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入昵称", trigger: "blur" },
          { min: 1, max: 8, message: "长度在 3 到 10 个字符", trigger: "blur" },
          {
            required: true,
            pattern: /^[\u4e00-\u9fa5_a-zA-Z0-9.·-]+$/,
            message: "姓名不支持特殊字符",
            trigger: "blur"
          }
        ],
        oldPassword: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            required: true,
            pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
            message: "密码应该是包含 数字和英文，长度6-20",
            trigger: "blur"
          }
        ],
        newPassword: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            required: true,
            pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
            message: " 密码应该是包含 数字和英文，长度6-20",
            trigger: "blur"
          },
          {
            validator: validateComfirmPwd,
            trigger: "blur"
          }
        ],
        birthday: [
          {
            type: "date",
            required: true,
            message: "请填写正确的日期",
            trigger: "change"
          }
        ],
        eMail: [
          { type: "email", message: "请填写正确的邮箱", trigger: "blur" },
          { required: true, message: "请输入邮箱", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style>
@import "../assets/css/index.css";
</style>



